import React from 'react'
import { useRouter } from 'next/router'
import Social from '../blocks/social'
import Interests from '../blocks/interests'
import Users from '../blocks/users'
import ProjectTypeIconContainer from '../icons/containers/projectType'

const BlockProject = (props) => {
    const {project = {}} = props;
    const router = useRouter();


    const handleClick = e => {
        if (!project.linked) return;

        e.preventDefault()
        router.push(`/project/${project.id}`);
      }

    return (
       
                <div className="block">
                    <div className="left">
                        
                            <div className="header clickable" onClick={handleClick}>
                                <ProjectTypeIconContainer type={project.type} certified={project.certified} />
                                <div className="title">{project.title}</div>

                            </div>

                            <div className="content clickable" onClick={handleClick}>
                                <div className="text">{project.text} </div>
                            </div>

                            <div className="footer">
                                <Interests interests={project.interests} />
                                <div className="users">By <Users users={project.users}/></div>
                                <Social social={project.social} />
                            </div>
                        

                    </div>

                    <div className="right clickable" onClick={handleClick}></div>

                    <style jsx>
                {`  

                    .clickable {
                        
                        ${project.linked ? 'cursor: pointer;\n' : ''}
                    }
                    .block {
                        background-color: #F4F6F6;
                        border: 1px solid #D3DDDD;
                        border-radius: 5px;
                        height: 280px;
                        width: 640px;

                        color: #707070;
                        letter-spacing: 0;

                        display: flex;
                        flex-direction: row;
                        
                    }
                    .header {
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        font: Bold 14px/19px Arial;
                        letter-spacing: 0;
                        color: #000000;

                        
                        padding-top: 18px;
                        padding-left: 18px;

                        
                    }

                    .title {
                        width: 300px;
                        margin-left: 10px;
                    }
                    .left {
                        width: 370px;

                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                    }
                    .right {
                        width: 270px;
                        border-radius: 0px 5px 5px 0px;
                        background: url('/media/projects/${project.id}.png')  no-repeat;
                    }

                    .content {
                        display: flex;
                        flex-direction: column;
                        align-items: center;

                        height: 100%;

                        padding-top: 18px;
                        padding-left: 18px;
                    }

                    .text {
                        font: 12px/17px Arial;
                        letter-spacing: 0;
                        color: #707070;   

                        width: 330px;                     

                    }

                    .footer div {
                        margin-top: 10px;
                        margin-bottom: 10px;
                    }

                    .footer {
                        padding: 0px 18px 5px 18px;
                    }

                    .users {
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        font: 12px/17px Arial;
                        letter-spacing: 0;
                        color: #707070;
                    }

                `}

                </style>


                </div>

    )
}

export default BlockProject;